.editor-styles-wrapper .jobs-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0; }

.editor-styles-wrapper .job-container {
  flex-grow: 0;
  background: #e1e1e1;
  padding: 10px 14px;
  margin-bottom: 14px;
  cursor: not-allowed;
  opacity: 0.4;
  font-size: 0.75em;
  overflow: hidden;
  word-break: break-word;
  flex: 0 0 calc(50% - 7px);
  max-width: calc(50% - 7px); }
  .editor-styles-wrapper .job-container h3 {
    margin: 0;
    font-size: 1.25em;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; }
  .editor-styles-wrapper .job-container:nth-of-type(even) {
    margin-left: 14px; }

.editor-styles-wrapper .simple-notice,
.editor-styles-wrapper .jobs .jobs-notice {
  color: #ff00b1;
  border: 1px solid #ff00b1;
  cursor: not-allowed;
  opacity: 0.4;
  padding: 0.5em;
  text-align: center; }

.editor-styles-wrapper .clients-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }
  .editor-styles-wrapper .clients-container .client {
    display: flex;
    justify-content: center; }

.editor-styles-wrapper .benefit-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 1em; }
  .editor-styles-wrapper .benefit-container .benefit {
    flex-basis: 33.33333%;
    max-width: 33.33333%;
    text-align: center; }
    .editor-styles-wrapper .benefit-container .benefit img {
      max-height: 60px;
      max-width: 60px; }
    .editor-styles-wrapper .benefit-container .benefit .benefit-text {
      font-size: 85%;
      line-height: 1.3;
      margin: 1em 0 1.5em; }

.editor-styles-wrapper .value-container {
  margin-bottom: 1.5em; }
  .editor-styles-wrapper .value-container .value-heading {
    display: inline-block;
    margin: 0 0 0.25em; }
  .editor-styles-wrapper .value-container .value-text {
    margin: 0; }

.editor-styles-wrapper .team {
  border: 1px solid #d4d4d4;
  padding: 0.5em; }
  .editor-styles-wrapper .team .team-container {
    display: flex;
    flex-wrap: wrap; }
    .editor-styles-wrapper .team .team-container .employee {
      padding: 0.5em;
      flex-basis: 50%;
      max-width: 50%; }
      .editor-styles-wrapper .team .team-container .employee .employee-name,
      .editor-styles-wrapper .team .team-container .employee .employee-role {
        font-size: 1rem;
        margin: 0; }

.editor-styles-wrapper .clients-container {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 1em; }
  .editor-styles-wrapper .clients-container .client {
    padding: 0.5em;
    flex-basis: 33.33333%;
    max-width: 33.33333%;
    display: flex;
    align-items: center; }
    .editor-styles-wrapper .clients-container .client img {
      max-width: 60%; }

.editor-styles-wrapper .testimonials .testimonial-image {
  max-width: 200px;
  height: 50px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain; }

.editor-styles-wrapper .testimonials .testimonial-blockquote {
  margin: 0.5em 0 1em;
  font-size: 1rem; }
  .editor-styles-wrapper .testimonials .testimonial-blockquote span {
    display: block; }
  .editor-styles-wrapper .testimonials .testimonial-blockquote .testimonial-text {
    font-size: 1rem; }
  .editor-styles-wrapper .testimonials .testimonial-blockquote .testimonial-author {
    font-size: 0.75rem; }

.editor-styles-wrapper .testimonials[data-columns="3"] .testimonials-slider {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -0.5em; }
  .editor-styles-wrapper .testimonials[data-columns="3"] .testimonials-slider .testimonials-slide {
    padding: 0.5em;
    flex-basis: 33.33333%;
    max-width: 33.33333%; }

.editor-styles-wrapper .demo-container {
  display: flex;
  flex-wrap: wrap;
  margin: 0; }
  .editor-styles-wrapper .demo-container .demo-col {
    flex-grow: 0;
    overflow: hidden;
    word-break: break-word;
    flex: 0 0 calc(50% - 7px);
    max-width: calc(50% - 7px); }
    .editor-styles-wrapper .demo-container .demo-col:nth-of-type(even) {
      margin-left: 14px; }
  .editor-styles-wrapper .demo-container .demo-actions,
  .editor-styles-wrapper .demo-container .demo-select,
  .editor-styles-wrapper .demo-container .demo-label {
    display: none; }

.editor-styles-wrapper .chart-block .wp-block-column {
  margin-left: 0; }

.editor-styles-wrapper .chart-block .chart-container {
  border: 1px solid #d4d4d4;
  padding: 0.5em; }
  .editor-styles-wrapper .chart-block .chart-container > p {
    margin-top: 0; }

.editor-styles-wrapper .chart-block .chart-legend-wrapper {
  display: none; }

.editor-styles-wrapper .chart-block .bar {
  padding: 0.5em;
  margin-bottom: 0.5em;
  text-align: right; }
  .editor-styles-wrapper .chart-block .bar.bar-2 {
    margin-bottom: 0; }
  .editor-styles-wrapper .chart-block .bar .bar-value {
    margin-left: 0.5em; }
